import { Component, OnInit, Input, Output, EventEmitter, ViewChild, Inject } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { PermissionService, Permission } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';


@Component({
  selector: 'app-permission-edit',
  templateUrl: './permission-edit.component.html',
  styleUrls: ['./permission-edit.component.scss']
})
export class PermissionEditComponent implements OnInit {

  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @Input('PermissionEditData') PermissionEditData;

  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  permissionForm: FormGroup;
  apiUrl: string;
  permissionData: any;

  constructor(
    private fb: FormBuilder,
    private perms: PermissionService,
    @Inject('API_URL') apiUrl: string
  ) {
    this.createForm();
    this.apiUrl = apiUrl;
  }

  ngOnInit() {
    this.permissionData = new Permission(this.PermissionEditData);
    this.permissionForm.setValue(this.permissionData);
  }

  createForm() {
    this.permissionForm = this.fb.group({
      'name': [null, Validators.required],
      'display_name': [null, Validators.required],
      'description': [null],
    });
  }

  saveBtn(post){
    this.jqxLoader.open();
    this.perms.update(this.PermissionEditData.id, post).subscribe(
      result => {
        let data = {
          result:true,
          message:result['message']
        }
        this.UpdatedData.emit(data);
        this.jqxLoader.close();
      },
      error => {
        let data = {
          result:false,
          message:error['error']['error']['message']
        }
        this.UpdatedData.emit(data);
        this.jqxLoader.close();
    });
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
  [
    { input: '.name', message: 'Name is required !', action: 'keyup, blur', rule: 'required' },
    { input: '.display_name', message: 'Display Name is required!', action: 'keyup, blur', rule: 'required' },
  ];

}



