import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PermissionComponent } from './permission.component';
import { SharedModule } from '../../../shared/modules/shared.module';
import { PermissionRoutingModule } from './permission-routing.module';
import { PermissionAddComponent } from './permission-add/permission-add.component';
import { PermissionEditComponent } from './permission-edit/permission-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    PermissionRoutingModule
  ],
  declarations: [PermissionComponent, PermissionAddComponent, PermissionEditComponent],
  entryComponents:[PermissionAddComponent, PermissionEditComponent]
})
export class PermissionModule { }



